.shard-docs {
  font-family: -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Ubuntu, Arial, sans-serif;
  display: flex;
  height: 100vh;
  width: 100%;
  max-width: 100%; }

.shard-docs-main {
  padding: 15px 30px;
  flex: 1;
  background: white;
  overflow: scroll; }

.shard-docs-sidebar {
  margin: 0;
  border-right: thin solid #dddddd;
  overflow-y: scroll;
  background: #673ab7;
  color: rgba(255, 255, 255, 0.5);
  font-size: 14px; }

.shard-docs-sidebar-header {
  width: 304px;
  margin-top: 16px; }

.shard-docs-sidebar-header a {
  text-decoration: none; }
  .shard-docs-sidebar-header a:hover {
    text-decoration: underline; }

.shard-docs-sidebar-heading {
  background: white;
  margin: 0;
  padding: 3px 30px 3px 15px;
  color: #673ab6;
  font-size: 14px;
  font-weight: 700; }

.shard-docs-sidebar-description {
  padding: 15px 15px 5px 15px;
  color: white;
  margin-bottom: 0;
  margin-top: 0;
  font-size: 14px;
  cursor: text;
  font-weight: 100; }

.shard-docs-sidebar-menu {
  margin: 0;
  padding: 7px 15px;
  width: 320px;
  color: rgba(255, 255, 255, 0.5);
  list-style-type: none;
  line-height: normal; }
  .shard-docs-sidebar-menu a {
    color: rgba(255, 255, 255, 0.9);
    text-decoration: none;
    font-weight: 100; }
    .shard-docs-sidebar-menu a:hover {
      text-decoration: underline; }
  .shard-docs-sidebar-menu ul {
    padding-left: 15px;
    list-style-type: none; }
  .shard-docs-sidebar-menu li {
    margin: 8px 0;
    font-size: 15px; }
  .shard-docs-sidebar-menu > li:first-child > * {
    margin-top: 0; }
  .shard-docs-sidebar-menu h3 {
    cursor: text;
    font-size: 15px;
    font-weight: 700;
    color: white;
    margin: 8px 0;
    display: inline-block; }
  .shard-docs-sidebar-menu .active {
    font-weight: 500;
    color: #fff176; }

.docsheet {
  width: 840px;
  margin: 0 15px 15px 0;
  vertical-align: top;
  background: white; }
  .docsheet > * {
    cursor: initial; }

.docsheet > :first-child {
  margin-top: 15px; }

.docsheet h1 {
  font-weight: 500; }

.docsheet h2 {
  font-weight: 500; }

.docsheet h3 {
  font-weight: 500; }

.docsheet pre code {
  background: inherit;
  color: inherit;
  display: block;
  padding: 15px;
  font-size: 13px;
  border: none; }

.docsheet code {
  padding: 2px 6px;
  border-radius: 2px;
  font-size: 12px;
  font-family: Consolas, Monaco, Lucida Console, Liberation Mono, DejaVu Sans Mono, Bitstream Vera Sans Mono, Courier New;
  background: #f5f5f5;
  color: #e0245e; }

.docsheet pre {
  background: #2a2021;
  background: #282c34;
  background: #151515;
  background: #f4f2f0;
  color: white;
  color: #d0d0d0;
  color: black;
  border-radius: 4px;
  margin-bottom: 25px;
  overflow-x: scroll; }

.docsheet p {
  margin-top: 5px; }

.docsheet table {
  word-break: normal;
  line-height: 1.4;
  font-size: 14px;
  margin-bottom: 25px;
  margin-top: 10px; }

.docsheet table thead {
  background: #f5f5f5;
  white-space: nowrap;
  font-weight: bold; }

.docsheet table thead th {
  font-weight: 600;
  text-align: left; }

.docsheet table th,
.docsheet table td {
  border: thin solid white;
  padding: 7px;
  line-height: 1.8;
  vertical-align: top; }

.docsheet table td.param {
  color: #008000;
  font-family: "Courier New", Courier, monospace; }

.docsheet table tbody tr {
  background: #f5f5f5; }

.docsheet table tbody tr:nth-child(even) {
  background: white; }

.shard-docs-breadcrumbs {
  margin: 0;
  padding: 0;
  list-style-type: none;
  border-bottom: thin solid #dddddd;
  padding-bottom: 15px; }
  .shard-docs-breadcrumbs > * {
    cursor: initial; }
  .shard-docs-breadcrumbs a {
    color: #0000ee;
    text-decoration: none; }
  .shard-docs-breadcrumbs a:hover {
    text-decoration: underline; }

.shard-docs-breadcrumbs li {
  display: inline-block; }
  .shard-docs-breadcrumbs li:not(:last-child):after {
    content: " / ";
    margin-right: 4px; }

.shard-docs-breadcrumbs li:last-child a.active {
  color: black;
  text-decoration: none;
  cursor: text; }

.shard-docs-page-nav {
  display: flex;
  margin-top: 40px;
  border-top: thin solid #dddddd;
  padding: 15px 0 45px 0;
  display: flex; }
  .shard-docs-page-nav a {
    color: #0000ee;
    text-decoration: none;
    font-size: 14px; }
  .shard-docs-page-nav a:hover {
    text-decoration: underline; }

.shard-docs-page-spacer {
  flex: 1;
  text-align: center; }

.shard-docs-iframe-shard {
  width: 840px;
  height: 400px;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1), 0 0 40px rgba(0, 0, 0, 0.1);
  margin-top: 40px; }

.shard-docs-markdown-shard {
  width: 840px;
  margin: 0 15px 15px 0;
  vertical-align: top;
  background: white; }
  .shard-docs-markdown-shard > * {
    cursor: initial; }
  .shard-docs-markdown-shard > :first-child {
    margin-top: 15px; }
  .shard-docs-markdown-shard h1,
  .shard-docs-markdown-shard h2,
  .shard-docs-markdown-shard h3 {
    font-weight: 500; }
  .shard-docs-markdown-shard pre code {
    background: inherit;
    color: inherit;
    display: block;
    padding: 15px;
    font-size: 13px;
    border: none; }
  .shard-docs-markdown-shard code {
    padding: 2px 6px;
    border-radius: 2px;
    font-size: 12px;
    font-family: Consolas, Monaco, Lucida Console, Liberation Mono, DejaVu Sans Mono, Bitstream Vera Sans Mono, Courier New;
    background: #f5f5f5;
    color: #e0245e; }
  .shard-docs-markdown-shard pre {
    background: #2a2021;
    background: #282c34;
    background: #151515;
    background: #f4f2f0;
    color: white;
    color: #d0d0d0;
    color: black;
    border-radius: 4px;
    margin-bottom: 25px;
    overflow-x: scroll; }
  .shard-docs-markdown-shard p {
    margin-top: 5px; }
  .shard-docs-markdown-shard a {
    color: #0000ee; }
  .shard-docs-markdown-shard table {
    word-break: normal;
    line-height: 1.4;
    font-size: 14px;
    margin-bottom: 25px;
    margin-top: 10px; }
  .shard-docs-markdown-shard table thead {
    background: #f5f5f5;
    white-space: nowrap;
    font-weight: bold; }
  .shard-docs-markdown-shard table thead th {
    font-weight: 600;
    text-align: left; }
  .shard-docs-markdown-shard table th,
  .shard-docs-markdown-shard table td {
    border: thin solid white;
    padding: 7px;
    line-height: 1.8;
    vertical-align: top; }
  .shard-docs-markdown-shard table td.param {
    color: #008000;
    font-family: "Courier New", Courier, monospace; }
  .shard-docs-markdown-shard table tbody tr {
    background: #f5f5f5; }
  .shard-docs-markdown-shard table tbody tr:nth-child(even) {
    background: white; }

/**
 * prism.js default theme for JavaScript, CSS and HTML
 * Based on dabblet (http://dabblet.com)
 * @author Lea Verou
 */
code[class*="language-"],
pre[class*="language-"] {
  color: black;
  background: none;
  text-shadow: 0 1px white;
  font-family: Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
  font-size: 1em;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  word-wrap: normal;
  line-height: 1.5;
  -moz-tab-size: 4;
  -o-tab-size: 4;
  tab-size: 4;
  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none; }

pre[class*="language-"]::-moz-selection,
pre[class*="language-"] ::-moz-selection,
code[class*="language-"]::-moz-selection,
code[class*="language-"] ::-moz-selection {
  text-shadow: none;
  background: #b3d4fc; }

pre[class*="language-"]::selection,
pre[class*="language-"] ::selection,
code[class*="language-"]::selection,
code[class*="language-"] ::selection {
  text-shadow: none;
  background: #b3d4fc; }

@media print {
  code[class*="language-"],
  pre[class*="language-"] {
    text-shadow: none; } }

/* Code blocks */
pre[class*="language-"] {
  margin: 0.5em 0;
  overflow: auto; }

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
  background: #f5f2f0; }

/* Inline code */
:not(pre) > code[class*="language-"] {
  padding: 0.1em;
  border-radius: 0.3em;
  white-space: normal; }

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: slategray; }

.token.punctuation {
  color: #999; }

.namespace {
  opacity: 0.7; }

.token.property,
.token.tag,
.token.boolean,
.token.number,
.token.constant,
.token.symbol,
.token.deleted {
  color: #905; }

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: #690; }

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string {
  color: #9a6e3a;
  background: rgba(255, 255, 255, 0.5); }

.token.atrule,
.token.attr-value,
.token.keyword {
  color: #07a; }

.token.function,
.token.class-name {
  color: #dd4a68; }

.token.regex,
.token.important,
.token.variable {
  color: #e90; }

.token.important,
.token.bold {
  font-weight: bold; }

.token.italic {
  font-style: italic; }

.token.entity {
  cursor: help; }
